import { Component, OnInit } from '@angular/core';
import { ApiService } from '../api.service';
import { NgxLoaderSpinnerService } from 'ngx-loader-spinner';
import { ToastrService } from 'ngx-toastr';
import { Router } from '@angular/router';
@Component({
  selector: 'app-terms',
  templateUrl: './terms.component.html',
  styleUrls: ['./terms.component.css'],
})
export class TermsComponent implements OnInit {
  listing: any = [];
  vendor: any;
  p: number[] = [];
  collection: any[];
  pid: any;
  constructor(
    public apiService: ApiService,
    public spinnerService: NgxLoaderSpinnerService,
    public toastr: ToastrService,
    public router: Router
  ) {
    this.getTerms();
  }

  ngOnInit() {}
  getTerms() {
    this.spinnerService.show();
    this.apiService.getTerms().subscribe(
      (response) => {
        console.log(response);
        this.spinnerService.hide();
        this.listing = response.data[0];
        console.log(this.listing);
        this.pid = response.data._id;
      },
      (err) => {
        this.toastr.error('Something went wrong.Please try later.');
      }
    );
  }
  editDetails() {
    this.router.navigateByUrl('/edit-terms');
  }
}
