import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { AppComponent } from './app.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { SidebarComponent } from './sidebar/sidebar.component';
import { UserListComponent } from './user-list/user-list.component';
import { NgxPaginationModule } from 'ngx-pagination';
import { HeaderComponent } from './header/header.component';
import { LoginComponent } from './login/login.component';
import { OrdersComponent } from './orders/orders.component';
import { ProfileComponent } from './profile/profile.component';
import { TransactionsListComponent } from './transactions-list/transactions-list.component';
import { PrivacyComponent } from './privacy/privacy.component';
import { TermsComponent } from './terms/terms.component';
import { CustomerDetailsComponent } from './customer-details/customer-details.component';
import { EditCustomerComponent } from './edit-customer/edit-customer.component';
import { EditPrivacyComponent } from './edit-privacy/edit-privacy.component';
import { FooterComponent } from './footer/footer.component';
import { AddCustomerComponent } from './add-customer/add-customer.component';
import { EditTermsComponent } from './edit-terms/edit-terms.component';
import { DiffrentLevelsComponent } from './diffrent-levels/diffrent-levels.component';

const routes: Routes = [
  { path: 'dashboard', component: DashboardComponent },
  { path: 'profile', component: ProfileComponent },

  { path: '', component: LoginComponent },
  { path: 'footer', component: FooterComponent },
  { path: 'diff-levels', component: DiffrentLevelsComponent },
  { path: 'user', component: UserListComponent },
  { path: 'customer-details/:id', component: CustomerDetailsComponent },
  { path: 'edit-customer/:id', component: EditCustomerComponent },
  { path: 'add-customer', component: AddCustomerComponent },
  { path: 'terms', component: TermsComponent },
  { path: 'all-levels', component: OrdersComponent },

  { path: 'privacy', component: PrivacyComponent },
  { path: 'edit-privacy', component: EditPrivacyComponent },
  { path: 'edit-terms', component: EditTermsComponent },

  { path: 'points', component: TransactionsListComponent },
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash: true })],
  exports: [RouterModule],
})
export class AppRoutingModule {}
