import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ApiService } from '../api.service';
import { NgxLoaderSpinnerService } from 'ngx-loader-spinner';
import { ToastrService } from 'ngx-toastr';
import { Location } from '@angular/common';
import { Router } from '@angular/router';
import * as ClassicEditor from '@ckeditor/ckeditor5-build-classic';
@Component({
  selector: 'app-edit-privacy',
  templateUrl: './edit-privacy.component.html',
  styleUrls: ['./edit-privacy.component.css'],
})
export class EditPrivacyComponent implements OnInit {
  authForm: FormGroup;
  details: any;
  Image: any = null;
  selectedFile: any;
  paymentInfo: any = '';
  public Editor = ClassicEditor;
  constructor(
    public apiService: ApiService,
    public fb: FormBuilder,
    public spinnerService: NgxLoaderSpinnerService,
    public toastr: ToastrService,
    private _location: Location,
    public router: Router
  ) {
    // this.getPrivacy();
  }

  ngOnInit() {
    this.authForm = this.fb.group({
      desc: ['', Validators.compose([Validators.required])],
    });
    this.createForm();
  }
  /*getPrivacy() {
    this.apiService.getPrivacy().subscribe((response) => {
      this.details = response.data[0];
    });
  }*/

  createForm() {
    this.apiService.getPrivacy().subscribe((response) => {
      this.details = response.data[0];

      this.authForm = this.fb.group({
        userid: [this.details._id],
        desc: [this.details.desc, Validators.compose([Validators.required])],
      });
    });
  }

  updatePrivacy() {
    //	if(this.Image == null){
    this.spinnerService.show();
    let dict = this.authForm.value;
    console.log('PRIVACY POLICY DATA', dict);
    dict.userid = this.details._id;
    dict.desc = dict.desc;
    this.apiService.updatePrivacy(dict).subscribe((response) => {
      this.spinnerService.hide();

      if (response.status == true) {
        this._location.back();
        this.toastr.success('Privacy Policy has been updated successfully!');
      } else {
        this.toastr.error('Privacy Policy Not Updatd!');
      }
    });
  }
  backView() {
    this._location.back();
  }
}
